@keyframes slideInFromRight {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes slideInFromTop {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(0%);
  }
}



.from-right{
    /* border: blue solid 1px; */
  /* font-family: sans-serif; */
  margin: 5em 0 3em 0;
  z-index:1;
  display: block;
  text-align: left;
  /* min-height:0.3em; */
  /* min-height:0.5em; */
  min-height:0.5em;
  max-width:inherit;
  /* color:white; */
  background: linear-gradient(to right, #FF0095  0%, white 100%);
  /* background: #FF0095; */
background-size: 200% 100%;
background-position:left bottom;
animation: 4s ease-out 0s 1 slideInFromRight;
border-radius:0.3em;
}

.intro-div:hover + .from-right{
  background:#fabce0 ;
  transition: 1s ease;
  }


.proj-demo-link > h3 {
text-align:left;
margin-top: 2em ;
/* margin-right:4em; */
}

.proj-demo-thumb-container {
  /* border: 3px solid yellowgreen; */
  margin-top: 2em ;
  /* display:flex;
  flex-wrap: wrap; */
  padding:1em;
  position: relative;
  width: 30%;
}

.proj-demo-img{
  opacity: 1;
  width:100%;
  height:auto;
  transition: .3s ease;
  backface-visibility: hidden;
  display: block;
}

.proj-thumb-middle{
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transition: .3s ease;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  /* below will overlap the text */
  width:100%;
  height:100%;
  /* to place title in center */
  display:flex;
  justify-content: center; 
  align-items: center;
}

.proj-demo-thumb-container:hover .proj-demo-img{
  opacity:0.7;
}


.proj-demo-thumb-container:hover .proj-thumb-middle{
  opacity: 1;
  content:"\A";
  width:95%;
  height:90%;
  /* background:rgba(255, 0, 0, 0.5); */
  background:rgba(255, 48, 48, 0.616);
  /* background:rgba(0, 255, 255, 0.5); */
}

.proj-thumb-title{
  color: white;
  padding: 1em 2em;
 font-size: 1.5rem;
}


